import React from 'react';
import PropTypes from 'prop-types';
import {taskStore} from "../../stores/TaskStore";
import {putTask} from "../../stores/Api";

export default class Droppable extends React.Component{
    drop = (e) => {
        e.preventDefault();
        const data = e.dataTransfer.getData('transfer');
        console.log(data + "transfered into " + e.target.id);
        if(e.target.id.toString() === "To-Do") {
            taskStore.taskList.map((task, key) => {
                if(task.id===data){
                    updateTaskFunc(key);
                    console.log("task status has changed to: " + task.status);

                }
            })
        } else if (e.target.id.toString() === "In-Progress") {
            taskStore.taskList.map((task, key) => {
                if(task.id===data){
                    updateTaskFunc(key);
                    console.log("task status has changed to: " + task.status);

                }
            })
        } else if (e.target.id.toString() === "Completed") {
            taskStore.taskList.map((task, key) => {
                if(task.id===data){
                    updateTaskFunc(key);
                    console.log("task status has changed to: " + task.status);

                }
            })
        }
        e.target.appendChild(document.getElementById(data));
    }

    allowDrop = (e) => {
        e.preventDefault();
    }

    render(){
        return (
            <div id={this.props.id} onDrop={this.drop} onDragOver={this.allowDrop} style={this.props.style}>
                {this.props.children}
            </div>
        );
    }
}

Droppable.propTypes = {
    id: PropTypes.string,
    style: PropTypes.object,
    children: PropTypes.node,
}

function updateTaskFunc(key) {
    return (e) => {
        e.preventDefault();
            taskStore.taskList[key].status = "NotStarted";
            putTask(taskStore.taskList[key]);

        taskStore.updateTask = {
            name: '',
            description: '',
            id: '',
            responsible: '',
            status: ''
        };
    }
}